const saveDataToStorage = (token, userId, email, firstName, lastName, username, isActive) => {
  localStorage.setItem(
    'userData',
    JSON.stringify({
      token,
      userId,
      email,
      firstName,
      lastName,
      username,
      isActive
    })
  );
};

const getDataFromStorage = (itemTitle) => {
  return JSON.parse(localStorage.getItem(itemTitle));
};
